import axios from "axios";

export default class makeAxios {
  constructor(url, method, data, isWithCredentials = false) {
    this.url = process.env.VUE_APP_BACKURL + url;
    this.method = method;
    this.data = data
    this.withCredentials = isWithCredentials
  }
  doAxios(self, successFunction, failFunction) {
    self.$store.commit("LOADING", true);
    axios({
      url: this.url,
      method: this.method,
      data: this.data,
      withCredentials: this.withCredentials
    })
      .then(function (response) {
        if (response.data.success === 'not') {
          self.$router.push('/login')
        } else if(response.data.success){
          successFunction(response)
        }else{
          failFunction(response)
        }
        self.$store.commit("LOADING", false);
      })
      .catch(function (error) {
        failFunction(error)
        self.$store.commit("LOADING", false);
      });
  }
}